#more-btn {
  position: relative;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.0), #eaeaeadc);
  border: none;
  font-size: 1.38rem;
  color: black;
  font-weight: 500;
  cursor: pointer;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  transform: translateX(-10px);
  white-space: 'pre-wrap';
  padding-left: '10px';
}

#more-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.0), rgba(255, 255, 255, 0.8));
  filter: blur(10px);
  z-index: -1;
  white-space: 'pre-wrap';
  padding-left: '10px';

}
